@import '../../main.scss';
.categories {
  max-width: 375px;
  padding-left: 16px;
  padding-right: 16px;

  background-color: antiquewhite;
  padding-top: 50px;
  padding-bottom: 100px;
  @include tablet {
    padding-top: 72px;
    padding-bottom: 200px;
    max-width: 768px;
    min-width: 768px;

    padding-left: 32px;
    padding-right: 32px;
  }
  @include desktop {
    max-width: 1440px;
    min-width: 1440px;
    padding-top: 100px;
    padding-left: 100px;
    padding-right: 100px;
  }
  &-title {
    margin-bottom: 50px;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 28px;
    /* identical to box height, or 100% */

    letter-spacing: -0.02em;

    color: #001833;
    @include tablet {
      font-size: 32px;
      line-height: 32px;
    }
    @include desktop {
      margin-bottom: 100px;

      font-size: 44px;
      line-height: 44px;
    }
  }
  &-switcher {
    margin-bottom: 32px;
    @include tablet {
      margin-bottom: 50px;
    }
  }
  &-cards {
    display: flex;
    flex-wrap: wrap;

    gap: 32px;
    row-gap: 28px;
    // padding-bottom: 100px;
    @include tablet {
    }
    @include desktop {
      gap: 12px;
      row-gap: 100px;
    }
  }
}
